                    alt
                    width="100%"
                  />
                </el-form-item>
                <el-form-item label="">
                  <el-slider v-model="config.scale" :max="200" :format-tooltip="formatTooltip"></el-slider>
                </el-form-item>
                <el-form-item label="">
                  <avue-input type="textarea" :min-rows="2" v-model="config.url"></avue-input>
                </el-form-item>
                <el-form-item label="">
                  <el-button size="mini" type="primary" @click="openCode('query')"></el-button>
                </el-form-item>
                <el-form-item label="()">
                  <avue-switch v-model="config.mark.show"></avue-switch>
                </el-form-item>
                <template v-if="config.mark.show">
                  <el-form-item label="">
                    <avue-input v-model="config.mark.text"></avue-input>
                  </el-form-item>
                  <el-form-item label="">
                    <avue-input-number v-model="config.mark.fontSize"></avue-input-number>
                  </el-form-item>
                  <el-form-item label="">
                    <avue-color v-model="config.mark.textStyle"></avue-color>
                  </el-form-item>
                  <el-form-item label="">
                    <avue-input-number v-model="config.mark.degree"></avue-input-number>
                  </el-form-item>
                </template>
              </template>
            </el-form>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane name="1" v-if="vaildProp('dataList')">
            <el-tooltip slot="label" effect="dark" content="" placement="top">
              <div>
                <i class="el-icon-document-copy"></i>
              </div>
            </el-tooltip>
            <el-form label-width="120px" label-position="left" size="mini">
              <el-form-item label="">
                <avue-radio v-model="activeObj.dataType" :dic="dicOption.dataType"></avue-radio>
              </el-form-item>
              <el-form-item label="" label-position="top" v-if="activeObj.dataType===0">
                <el-button size="mini" type="primary" @click="openCode('data')"></el-button>
              </el-form-item>
              <template v-if="activeObj.dataType===1">
                <el-form-item label="">
                  <avue-input type="textarea" :min-rows="6" v-model="activeObj.url"></avue-input>
                </el-form-item>
                <el-form-item label="" v-if="activeObj.dataType===1">
                  <avue-radio v-model="activeObj.dataMethod" :dic="dicOption.dataMethod"></avue-radio>
                </el-form-item>
                <el-form-item label="" v-if="activeObj.dataType===1">
                  <el-button size="mini" type="primary" @click="openCode('dataQuery')"></el-button>
                </el-form-item>
                <el-form-item label="">
                  <avue-input-number v-model="activeObj.time"></avue-input-number>
                </el-form-item>
              </template>
              <el-form-item label="">
                <el-button size="mini" type="primary" @click="openCode('dataFormatter')"></el-button>
              </el-form-item>
              <el-form-item label-width="0">
                <el-button size="mini" type="primary" class="block" @click="handleRefresh"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane name="2" v-if="vaildProp('eventList')">
            <el-tooltip slot="label" effect="dark" content="" placement="top">
              <div>
                <i class="el-icon-thumb"></i>
              </div>
            </el-tooltip>
            <el-form label-width="120px" label-position="left" size="mini">
              <el-form-item label="">
                <avue-select
                  multiple
                  v-model="activeObj.child.index"
                  :dic="childList"
                  :props="childProps"
                ></avue-select>
              </el-form-item>
              <el-form-item label="">
                <avue-input v-model="activeObj.child.paramName"></avue-input>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane name="3" v-if="vaildProp('formatterList')">
            <el-tooltip slot="label" effect="dark" content="" placement="top">
              <div>
                <i class="iconfont icon-peizhi"></i>
              </div>
            </el-tooltip>
            <el-form label-width="120px" label-position="left" size="mini">
              <el-form-item label="">
                <el-button size="mini" type="primary" @click="openCode('formatter')"></el-button>
              </el-form-item>
              <el-form-item label="" v-if="vaildProp('clickFormatterList')">
                <el-button size="mini" type="primary" @click="openCode('clickFormatter')"></el-button>
              </el-form-item>
              <el-form-item label="" v-if="vaildProp('labelFormatterList')">
                <el-button size="mini" type="primary" @click="openCode('labelFormatter')"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane name="4" v-if="isActive">
            <el-tooltip slot="label" effect="dark" content="" placement="top">
              <div>
                <i class="el-icon-folder"></i>
              </div>
            </el-tooltip>
            <el-form label-width="120px" label-position="left" size="mini">
              <el-form-item label="">
                <avue-input v-model="activeObj.index" disabled></avue-input>
              </el-form-item>
              <el-form-item label="X">
                <avue-input-number v-model="activeObj.left"></avue-input-number>
              </el-form-item>
              <el-form-item label="Y">
                <avue-input-number v-model="activeObj.top"></avue-input-number>
              </el-form-item>
              <el-form-item label="">
                <avue-input-number v-model="activeComponent.width"></avue-input-number>
              </el-form-item>
              <el-form-item label="">
                <avue-input-number v-model="activeComponent.height"></avue-input-number>
              </el-form-item>
            </el-form>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>

    <el-dialog title="" :visible.sync="code.box" @close="code.obj={}" width="60%">
      <codeedit ref="codeedit" v-model="code.obj"></codeedit>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="code.box=false"> </el-button>
        <el-button type="primary" @click="codeClose" size="small"> </el-button>
      </span>
    </el-dialog>
    <contentmenu ref="contentmenu"></contentmenu>
  </div>
</template>
<script>
import layer from "./group/layer";
import top from "./group/top";
import imglist from "./group/imglist";
import contentmenu from "./group/contentmenu";
import codeedit from "./group/code";
import { dicOption, tableOption, colorOption } from "@/option/config";
import init from "@/mixins/";
import { uuid } from "@/utils/utils";
export default {
  mixins: [init],
  data() {
    return {
      keys: {
        ctrl: false
      },
      loading: "",
      childProps: {
        label: "name",
        value: "index"
      },
      key: "",
      menuFlag: true,
      code: {
        box: false,
        type: "",
        obj: ""
      },
      form: {},
      dicOption: dicOption,
      tableOption: tableOption,
      colorOption: colorOption,
      tabsActive: 0
    };
  },
  components: {
    imglist,
    layer,
    codeedit,
    top,
    contentmenu
  },
  computed: {
    isFolder() {
      return this.activeObj.children;
    },
    isActive() {
      return this.active.length !== 0;
    },
    isSelectActive() {
      return this.active.length > 1;
    },
    childList() {
      return this.list.filter(ele => {
        if (["tabs", "slide"].includes(ele.component.prop)) {
          return false;
        }
        return true;
      });
    },
    activeComponent() {
      // this.activeObj["dataQuery"] = this.$refs.container.contain.visual.password;
      return this.activeObj.component || {};
    },
    activeOption() {
      return this.activeObj.option || {};
    },
    activeKey() {
      if (
        this.activeComponent.name === "table" ||
        this.activeComponent.name === "testTable"
      ) {
        return this.activeOption.column;
      } else if (
        [
          "bar",
          "line",
          "pie",
          "table",
          "testTable",
          "funnel",
          "barLine",
          "liquid",
          "bubble",
          "gauge",
          "arrow"
        ].includes(this.activeComponent.name)
      ) {
        return this.activeOption.barColor;
      }
      return [];
    },
    activeObj() {
      let result;
      if (this.validatenull(this.active)) {
        return {};
      }

      this.active.forEach(ele => {
        const item = this.findnav(ele, true);
        if (this.active.length > 1) {
          if (!result) result = [];
          result.push(item.obj);
        } else {
          result = item.obj;
        }
      });
      let dataQuery = {
        txdm: "",
        param: ""
      };
      dataQuery.txdm = result.name;
      dataQuery.param = this.$refs.container.contain.visual.visualParam;
      result.dataQuery = JSON.stringify(dataQuery);
      return result;
    }
  },
  watch: {
    menuFlag() {
      this.setResize();
    },
    overactive(n, o) {
      [o, n].forEach((ele, index) => {
        this.setActive(ele, index === 1, "setOverActive");
      });
    },
    active(n, o) {
      [o, n].forEach((ele, index) => {
        ele.forEach(item => {
          this.setActive(item, index === 1, "setActive");
        });
      });
      //
      this.$refs.contentmenu.hide();
      // 
      this.tabsActive = "0";
    }
  },
  created() {
    this.listen();
  },
  mounted() {
    this.initFun();
    // let that=this;
    // window.addEventListener("message", function(event) {
    //   console.log(event);
    //   that.$refs.container.initData();
    // });
  },
  methods: {
    codeClose() {
      let value = this.$refs.codeedit.getValue();
      if (this.validatenull(value)) {
        value = "{}";
      }
      try {
        if (["query", "data"].includes(this.code.type)) {
          value = JSON.parse(value, null, 4);
        }
        if (this.code.type === "query") {
          this.config.query = value;
        } else {
          this.activeObj[this.code.type] = value;
        }
        this.code.box = false;
      } catch (error) {
        this.$message.error("");
      }
    },
    openCode(type) {
      console.log(type);
      this.code.type = type;
      if (type === "query") {
        this.code.obj = this.config.query;
      } else {
        this.code.obj = this.activeObj[type];
      }
      this.code.box = true;
    },
    initFun() {
      ["setScale", "setResize"].forEach(ele => {
        this[ele] = this.$refs.container[ele];
      });
      ["handleAdd"].forEach(ele => {
        this[ele] = this.$refs.top[ele];
      });
    },
    // 
    handleContextMenu(e, item = {}) {
      if (!item.index) {
        return;
      }
      if (!this.isSelectActive) {
        this.active = [item.index];
      }
      setTimeout(() => {
        this.$refs.contentmenu.show(e.clientX, e.clientY);
      }, 0);
    },
    //
    listen() {
      document.onkeydown = e => {
        this.keys.ctrl = e.metaKey;
      };
      document.onkeyup = e => {
        this.keys.ctrl = e.metaKey;
      };
    },
    setActive(val, result, fun) {
      const obj = this.$refs.container.handleGetObj(val);
      if (obj) obj[0][fun](result);
    },
    //
    handleFloder() {
      let floder = {
        title: "",
        name: "",
        index: uuid(),
        children: []
      };
      this.active.forEach(index => {
        const params = this.findnav(index);
        floder.children.push(params.obj);
        delete params.parent.splice(params.count, 1);
      });
      this.nav.push(floder);
      this.handleInitActive();
    },
    //
    handleDeleteSelect() {
      this.$confirm(`?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.active.forEach(index => {
          const params = this.findnav(index);
          delete params.parent.splice(params.count, 1);
        });
        this.handleInitActive();
      });
    },
    vaildProp(name, list) {
      if (list) {
        return list.includes(this.activeComponent.prop);
      }
      return this.dicOption[name].includes(this.activeComponent.prop);
    },
    handleRefresh(tip) {
      this.$refs.container.handleRefresh(tip);
    },
    formatTooltip(val) {
      return parseInt(val);
    },
    rowSave(row, done) {
      this.activeKey.push(row);
      done();
    },
    rowDel(row, index) {
      this.activeKey.splice(index, 1);
    },
    rowUpdate(row, index, done) {
      this.activeKey.splice(index, 1, row);
      done();
    },
    //
    handleOpenImg(item, type) {
      this.$refs.imglist.openImg(item, type);
    },
    //
    handleSetimg(val, type) {
      if (type === "activeObj.data") {
        this.activeObj.data = val;
      }
      if (type === "activeObj.data.value") {
        this.activeObj.data.value = val;
      } else if (type === "activeOption.backgroundImage") {
        this.activeOption.backgroundImage = val;
      } else if (type === "activeOption.backgroundBorder") {
        this.activeOption.backgroundBorder = val;
      } else if (type === "activeOption.empBackgroundBorder") {
        this.activeOption.empBackgroundBorder = val;
      } else if (type === "config.backgroundImage") {
        this.config.backgroundImage = val;
      }
    }
  }
};
</script>
<style lang="scss">
@import "../styles/style.scss";
@import "../styles/font.css";
</style>